import axios from 'axios';

const baseURL = process.env.VUE_APP_BASE_URL;

export function listarFacturaciones(codempresa) {
    return axios.get(`${baseURL}facturaciones/list?codempresa=${codempresa}`)
}

export function buscarFacturacion(idFacturacion) {
    return axios.get(`${baseURL}facturaciones/search?idFacturacion=${idFacturacion}`)
}

export function crearFacturaciones(descripcion, codempresa) {
    return axios.post(`${baseURL}facturaciones/create`, {
        descripcion: descripcion,
        codempresa: codempresa
    })
}

export function eliminarFacturaciones(idFacturaciones) {
    return axios.delete(`${baseURL}facturaciones/delete?idFacturacion=${idFacturaciones}`)
}

export function editarFacturaciones(descripcion, id) {
    return axios.put(`${baseURL}facturaciones/edit`,{
        descripcion: descripcion,
        id: id
    })
}