<template>
    <section class="min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <div class="mb-8 flex justify-between items-center">
            <h3 class="title-vista text-2xl font-semibold text-neutral-600">Administrar facturación</h3>
            <button @click="openModal"
                class="py-2 px-3 rounded-xl bg-orange-400 text-white text-sm hover:bg-orange-500 transition duration-300">
                Crear nuevo
            </button>
        </div>
        <div class="overflow-x-auto shadow-md sm:rounded-lg">
            <TableFacturacion :listFacturaciones="listFacturaciones" @eliminar-facturacion="eliminarFacturacion" @editar-facturacion="editarFacturacion" />
        </div>
        <transition name="modal" mode="out-in">
            <ModalCreate v-show="openModalV" @close-modal="closeModal" @save-datos="saveDatos" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalDelete v-if="confirmDelete" @aceptar-eliminar="aceptarEliminar" @cancelar-eliminar="cancelarEliminar"
                pregunta="¿Estás seguro de eliminar esta unidad?" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalEditar v-if="openModalEdit" @aceptar-editar="aceptarEditar" @cancelar-editar="cancelarEditar"
                :idFacturacion="idFacturacion" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModaError v-if="openModalError" title="Advertencia" mensaje="Se debe rellenar todos los campos"
                @close-modal="closeModalError" />
        </transition>
        <transition name="modal" mode="out-in">
            <ConfirmEdit v-if="openSuccessEdit" title="Logrado" mensaje="Se ha editado correctamente"
                @close-modal="closeSuccessEdit" />
        </transition>
    </section>
</template>
<script>
import { ref, onMounted } from 'vue';
import { listarFacturaciones, crearFacturaciones, eliminarFacturaciones, editarFacturaciones } from '../services/FacturacionService';
import TableFacturacion from '../components/facturacion/TableFacturacion';
import ModalCreate from '../components/facturacion/ModalCreate';
import ModalDelete from '../components/generales/ModalDelete.vue';
import ModalEditar from '../components/facturacion/ModalEditar';
import ConfirmEdit from '../components/generales/ConfirmEdit.vue';
import ModaError from '../components/generales/ModalError.vue';
import store from '../store'

export default ({
    name: "AdminFacturacion",
    components: {
        TableFacturacion, ModalCreate,
        ModalDelete, ModalEditar,
        ModaError, ConfirmEdit
    },
    setup() {
        const openModalV = ref(false);
        const openModalEdit = ref(false);
        const openModalError = ref(false);
        const confirmDelete = ref(false);
        const openSuccessEdit = ref(false);
        const listFacturaciones = ref([]);
        const idFacturacion = ref('')
        const openModal = () => {
            openModalV.value = true;
        };

        const closeModal = () => {
            openModalV.value = false;
        }

        const closeSuccessEdit = () => {
            openSuccessEdit.value= false;
            window.location.reload();
        }

        const eliminarFacturacion = (idfacturacion) => {
            idFacturacion.value = idfacturacion
            confirmDelete.value = true;
        }

        const aceptarEliminar = () => {
            eliminarFacturaciones(idFacturacion.value)
                .then(response => {
                    console.log(response.data)
                    confirmDelete.value = false;
                    idFacturacion.value = '';
                    window.location.reload();
                })
                .catch((error) => {
                    console.log(error)
                })
        }

        const cancelarEliminar = () => {
            confirmDelete.value = false;
        }

        const cancelarEditar = () => {
            openModalEdit.value = false;
        }
        const editarFacturacion = (idfacturacion) => {
            idFacturacion.value = idfacturacion;
            openModalEdit.value = true;
        }

        const aceptarEditar = (dataformulario) => {
            const descripcion = dataformulario.descripcion;
            const id = dataformulario.id;
            if (descripcion == '') {
                openModalEdit.value = false;
                openModalError.value = true;
            } else {
                editarFacturaciones(descripcion, id)
                    .then(response => {
                        console.log(response.data)
                        openModalEdit.value = false;
                        openSuccessEdit.value= true;
                    })
            }
        }

        const closeModalError = () => {
            openModalError.value = false;
        }

        const saveDatos = (dataformulario) => {
            const descripcion = dataformulario.descripcion;
            if (descripcion == '') {
                openModalV.value = false;
                openModalError.value = true;
            } else {
                crearFacturaciones(descripcion, store.state.codempresa)
                    .then(response => {
                        console.log(response.data)
                        openModalV.value = false;
                        window.location.reload();
                    })
            }
        }
        onMounted(() => {
            listarFacturaciones(store.state.codempresa)
                .then(response => {
                    console.log(response.data)
                    listFacturaciones.value = response.data.facturaciones
                })
        })
        return {
            listFacturaciones,
            openModal,
            openModalV,
            openModalError,
            openSuccessEdit,
            closeModal,
            closeSuccessEdit,
            closeModalError,
            saveDatos,
            confirmDelete,
            eliminarFacturacion,
            aceptarEliminar,
            cancelarEliminar,
            cancelarEditar,
            editarFacturacion,
            openModalEdit,
            idFacturacion,
            aceptarEditar
        }
    }
})
</script>
<style scoped>
.modal-enter-active,
.modal-leave-active {
    transition: opacity 0.3s;
}

.modal-enter-from,
.modal-leave-to {
    opacity: 0;
}
</style>