<template>
    <table class="w-full text-xs text-left text-gray-800">
        <thead class="text-xs text-gray-700 uppercase bg-gray-100">
            <tr>
                <th scope="col" class="px-6 py-3">
                    Codigo de empresa
                </th>
                <th scope="col" class="px-6 py-3">
                    Descripción
                </th>
                <th scope="col" class="px-6 py-3">
                    Estado
                </th>
                <th scope="col" class="px-6 py-3">
                    Acciones
                </th>
            </tr>
        </thead>
        <tbody>
            <tr v-if="listFacturaciones.length === 0">
                <td class="border px-4 py-4 text-xs text-center" colspan="5">No se encontró ningún dato
                    para esta tabla</td>
            </tr>
            <tr v-else v-for="facturacion in listFacturaciones" :key="facturacion.id"
                class="bg-white border-b hover:bg-gray-50 ease-in duration-300">
                <td class="px-6 py-3">
                    {{ facturacion.codempresa }}
                </td>
                <th scope="row" class="px-6 py-3 font-medium text-gray-900 whitespace-nowrap">
                    {{ facturacion.descripcion }}
                </th>
                <td class="px-6 py-3">
                    <span class="rounded-xl bg-amber-500 px-3 py-2 text-white">Activo</span>
                </td>
                <td class="px-6 py-2 flex gap-2">
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-green-500 hover:bg-green-600 ease-in duration-300 text-white"
                        @click.prevent="editarFacturacion(facturacion.id)">Editar</button>
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-red-500 hover:bg-red-600 ease-in duration-300 text-white "
                        @click.prevent="eliminarFacturacion(facturacion.id)">Eliminar</button>
                </td>
            </tr>
        </tbody>
    </table>
</template>
<script>
export default ({
    name: "TableFacturacion",
    props: {
        listFacturaciones: Array
    },
    methods: {
        eliminarFacturacion(idFacturacion) {
            this.$emit('eliminar-facturacion', idFacturacion)
        },
        editarFacturacion(idFacturacion) {
            this.$emit('editar-facturacion', idFacturacion)
        }
    }
})
</script>